import * as yup from "yup";

const validationSchema = yup.object({
  usuario: yup.object({
    nome: yup
      .string()
      .min(10, "Nome deve conter no minimo 10 caracteres")
      .required("Nome  obrigatrio"),
    email: yup
      .string()
      .email("Insira um email valido")
      .required("Email  obrigatrio"),
  }),
  pessoa: yup.object({
    cpf: yup.string().required("CPF  obrigatrio"),
    celular: yup.string().required("Celular  obrigatrio"),
    nascimento: yup
      .string()
      .required("Nascimento  um campo obrigatrio")
      .nullable(),
    naturalidade: yup.string().required("Naturalidade  um campo obrigatrio"),
    nacionalidade: yup
      .string()
      .required("Nacionalidade  um campo obrigatrio"),
    rg: yup.string().required("RG  um campo obrigatrio"),
    emissor: yup.string().required("Orgo emissor  obrigatrio"),
    emissao: yup.string().required("Data de emisso  obrigatrio").nullable(),
    sexo: yup.string().required("Sexo  um campo obrigatrio"),
    estado_civil: yup.string().required("Estado civil  obrigatrio"),
    mae: yup.string().required("Me  um campo obrigatrio"),
    pai: yup.string().required("Pai  um campo obrigatrio"),
  }),
  conta: yup.object({
    banco: yup.string().required("Estado  um campo obrigatrio"),
    agencia: yup.string().required("Agncia  um campo obrigatrio"),
    conta: yup.string().required("Conta  um campo obrigatrio"),
    pix: yup.string().required("Pix  um campo obrigatrio"),
  }),
  endereco_cpf: yup.object({
    endereco: yup.string().required("Endereo  um campo obrigatrio"),
    cep: yup.string().required("CEP  um campo obrigatrio"),
    complemento: yup.string(),
    bairro: yup.string().required("Bairro  um campo obrigatrio"),
    numero: yup.string().required("Numero  um campo obrigatrio"),
    cidade: yup.string().required("Cidade  um campo obrigatrio"),
    estado: yup.string().required("Estado  um campo obrigatrio"),
  }),
});

export default validationSchema;
